import React from "react";
import styled from "styled-components";
import axios from "axios";
import { Link } from "react-router-dom";

class Artist extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: ""
    };
  }
  // export default ({ id, artist }) => {
  async componentDidMount() {
    const url = this.props.artist.tracklist;
    const proxyurl = "https://cors-anywhere.herokuapp.com/";
    const seperate = url.split("?")[0];
    const {
      data: { data }
    } = await axios.get(`${proxyurl}${seperate}?`, {
      params: {
        limit: 50
      },
      method: "GET",
      headers: {
        "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
        "x-rapidapi-key": "03afeef36cmsh1bdcf2e9a60f303p19457cjsn1f84e9465bd0"
      }
    });
    this.setState({
      data: data
    });
  }
  render() {
    const {
      props: { artist }
    } = this;
    return (
      <>
        {this.state.data ? (
          <Link
            to={{
              pathname: `/artist/${artist.name}`,
              state: { data: this.state.data },
              artist:artist.name
            }}
            style={{ textDecoration: "none", color: "black" }}
          >
            <Contaier>
              <Poster poster={artist.picture_medium}></Poster>
              <Name>{artist.name}</Name>
            </Contaier>
          </Link>
        ) : setTimeout(()=><h2>No Result Found</h2>, 2000)}
      </>
    );
  }
}
export default Artist;

const Contaier = styled.div`
  width: 100%;
  font-size: 1.1rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  grid-column-gap:20px;
  background-color: rgba(240, 240, 240, 0.5);
`;
const Poster = styled.div`
  background-image: url(${props => props.poster});
  width: 70px;
  height: 100px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
`;
const Name = styled.div`
padding-right:20px;
`;
