import React from "react";
import styled from "styled-components";

let currentAudio = "";
export function Artist_name_detail({
  id,
  title,
  preview,
  contributors,
  album
}) {
  const endedHandler = e => {
    console.log("ENDED");
    currentAudio = "";
  };
  const pauseHandler = e => {
    console.log("-----PAUSED");
  };
  const clickHandler = e => {
    const audio = e.currentTarget.nextElementSibling;
    //yes music before
    if (currentAudio) {
      //pass to pre
      const preAudio = document.querySelector(`#${currentAudio}`);
      preAudio.pause();
    }
    //set as now
    currentAudio = audio.id;
    audio.addEventListener("ended", endedHandler);
    audio.addEventListener("pause", pauseHandler);
    audio.play();
  };
  return (
    <Container>
      <Poster poster={album.cover_medium} track={album.tracklist}></Poster>
      <Album>{album.title}</Album>
      <Info>
        <Title>{title}</Title>
        {contributors.map((each, index) => {
          if (index < 3) {
            return contributors.length - 1 > index ? (
              <Contributors key={index}>{`${each.name} & `}</Contributors>
            ) : (
              <Contributors key={index}>{each.name}</Contributors>
            );
          } else if (index === 4) {
            return <Contributors key={index}>...</Contributors>;
          } else {
            return null;
          }
        })}
      </Info>
      <Button onClick={clickHandler}>
        <span role="img" aria-label="img">
          ðŸŽ¼
        </span>
      </Button>
      <audio className="audio" id={`ArtistName${id}`}>
        <source src={preview}></source>
      </audio>
    </Container>
  );
  // }
}

const Container = styled.div`
  width: 70%;
  display: grid;
  grid-template-columns: 0.5fr 3.5fr 3.5fr 0.5fr;
  grid-column-gap:20px;
  /* background-color:pink; */
  padding: 7px 0;
  border-bottom: 0.5px solid rgba(24, 21, 50, 0.55);
`;
const Poster = styled.div`
  background-image: url(${props => props.poster});
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 40px;
  height: 70px;
`;
const Album = styled.div`
  align-self: center;
`;
const Info = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
`;
const Title = styled.div`
  font-weight: bolder;
`;
const Contributors = styled.span``;
const Button = styled.button`
  font-size: 1.4rem;
  cursor: pointer;
  border: 0;
  outline: 0;
  background-color: rgba(0, 0, 0, 0);
  padding-right:20px;
  &:hover {
    transform: scale(1.1);
  }
  &:active {
    transform: scale(1);
  }
`;
