import React from "react";
import styled from "styled-components";
import Song from "../components/Song";
import axios from "axios";

// export function Album_title_detail({ id, title, artist, album}) {
export class Album_title extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: ""
    };
  }
  // export default ({ id, artist }) => {
  async componentDidMount() {
    const url = this.props.location.state.data[0].artist.tracklist;
    const proxyurl = "https://cors-anywhere.herokuapp.com/";
    const seperate = url.split("?")[0];
    const {
      data: { data }
    } = await axios.get(`${proxyurl}${seperate}?`, {
      params: {
        limit: 20
      },
      method: "GET",
      headers: {
        "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
        "x-rapidapi-key": "03afeef36cmsh1bdcf2e9a60f303p19457cjsn1f84e9465bd0"
      }
    });
    this.setState({
      data: data
    });
  }

  render() {
    const {
      props: {
        location: {
          state: { album, poster, title }
        }
      }
    } = this;
    return (
      <Container>
        <Info>
        <Poster poster={poster}></Poster>
          <Title>{title}</Title>
        </Info>
        <Songs>
          {this.state.data
            ? this.state.data.map((each, index) => {
                return (
                  <Song
                    key={index}
                    id={index}
                    title={each.title}
                    preview={each.preview}
                    artist={each.artist}
                    album={album}
                  ></Song>
                );
              })
            : null}
        </Songs>
      </Container>
    );
  }
}
const Container = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px 0;
`;
const Info = styled.div`
  display: flex;
  justify-content: center;
  align-items:center;
  margin:20px;
`;
const Poster = styled.div`
  background-image: url(${props => props.poster});
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 120px;
  height: 120px;
`;
const Title = styled.div`
  font-size:20px;
  margin-left:50px;
`;
const Songs = styled.div`
  width: 80%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  overflow-x: auto;
`;

export default Album_title;
