import React from "react";
import styled from "styled-components";
import { Artist_name_detail as Artist } from "../components/Artist_name_detail";
class Artist_name extends React.Component {
  render() {
    const {props:{location:{artist,state:{data}}}}=this;
    console.log(artist)
    return (
      <Container>
        <Title>{artist}</Title>
        {data?.map((each, index) => (
          <Artist key={index}
          id={index}
          title={each.title}
          preview = {each.preview}
          contributors={each.contributors}
          artist={each.artist}
          album={each.album}
          ></Artist>
        ))}
      </Container>
    );
    // }
  }
}
const Container = styled.div`
  width:100%;
  display:flex;
  flex-direction:column;
  align-items:center;
  margin:30px 0;
`;
const Title = styled.div`
font-size:30px;
`;
export default Artist_name;
