import React from 'react';
import {HashRouter, Route} from 'react-router-dom';
import Home from './Home';
import Result from '../routes/Result'
import Body from './Body'
import Artist_name from '../routes/Artist_name'
import Album_title from '../routes/Album_title'
function App() {
    
  return (
    <HashRouter>
      <Home></Home>
      <Route exact path="/" component = {Body}></Route>
      <Route exact path="/:id" component = {Result}></Route>
      <Route exact path="/artist/:id" component={Artist_name }></Route>
      <Route exact path="/album/:id" component={Album_title }></Route>
    </HashRouter>
  );
}

export default App;
